<template>
  <div class="home">
    <div class="d-flex justify-content-center align-items-center">
      <img src="/img/icons/android-icon-48x48.png" alt="방명록 이미지">
      <h2 class="ml-1 h3">방명록</h2>
    </div>
     <div class="mt-3">
      <!-- 추가 -->       
      <button v-if="this.permission === 'default'" @click="AskNotiPermission" class="btn btn-success w-100">
        알림 수신
      </button>
      <button v-else-if="this.permission === 'denied'" class="btn btn-success disabled w-100">알림 거절됨</button> 
      <button v-else class="btn btn-info w-100" @click="alimTest">테스트 알림</button>
    </div>
    <div class="row p-1 mt-4 ml-0 mr-0">
      <input type="text" v-model="context" class="form-control col-9">
      <div class="col-3 p-0">
        <button class="btn btn-primary w-100" @click="writePost">작성</button>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  //eslint-disable-next-line
  name: 'Home',
  beforeCreate() {
    
  },
  //추가
 created() {
    if('Notification' in window && 'serviceWorker' in navigator) {
      const permission = Notification.permission;
      //추가
      if(permission === 'granted') {
        this.createSubscription();
      }
      this.permission = permission;
    }
  },

  data() {
    return {
      posts : [],
      context : '',
      //추가
      permission : '',
      serverDataReceived : false
    }
  },
  methods : {
    urlBase64ToUint8Array(base64String) {
      const padding = '='.repeat((4 - base64String.length % 4) % 4);
      const base64 = (base64String + padding)
          .replace(/-/g, '+')
          .replace(/_/g, '/');

      const rawData = window.atob(base64);
      const outputArray = new Uint8Array(rawData.length);

      for (let i = 0; i < rawData.length; ++i) {
          outputArray[i] = rawData.charCodeAt(i);
      }
      return outputArray;
    },
 createSubscription() {
      //service worker를 지원하지 않으면 return
      if(!('serviceWorker' in navigator)) return;

      let reg;
      navigator.serviceWorker.ready.then( swreg => {
        reg = swreg;
        //subscription이 이미 있는지 조회
        return swreg.pushManager.getSubscription();
      }).then( sub => {
        //subscription이 없다면 생성
        if(sub === null) {
          const vapidPublicKey = 'BLpT0M0AxWlFKGpH7jTsBrrkkaW-WIvoU51DLnMzxwPcuP9K-be5E4OF65jFlkLQGWhb2j8PhR6XUBYrpvL6wZ8',
                convertedVapidPublicKey = this.urlBase64ToUint8Array(vapidPublicKey);

          return reg.pushManager.subscribe({
            userVisibleOnly : true,
            applicationServerKey : convertedVapidPublicKey
          });
        }
      }).then(newSub =>{
        if(!newSub) return;
    //생성한 subscription 서버에 저장
    this.$axios({
      url : `${this.serverHost}/push/subscriptions`,
      method : 'POST',
      data : { subscription : JSON.stringify(newSub) }
    }).then( res => {
      console.log("Save Subscription in server!!", res);
    }).catch( err => {
      console.error(err);    
    })
  }).catch( err => {
    console.error(err);
      })
    },

    AskNotiPermission() {
      Notification.requestPermission( result => {
        //추가
        if(result === 'granted') {
          this.createSubscription();
        }
        this.permission = result;
      });
    },
    alimTest() {
      new Notification('PUSH 알림 테스트~!!');
    },
    
  }
}
</script>
